<script>
    import { mobileCheck } from './core/Mobile.js'
    export let segment;
    let segments = [
        { id: 1, text: `home` },
        { id: 2, text: `about` },
        { id: 3, text: `fleeting dream` }
    ];

    function reveal() {
        var toggle = document.getElementById("toggleDrop");
        toggle.style.display = 'block';
    }

    function drop() {
        var toggle = document.getElementById("toggleDrop");
        var setting = toggle.style.display;
        if (setting == 'block') {
            toggle.style.display = 'none';
        } else {
            toggle.style.display = 'block';
        }
    }
</script>

